.gridContainer {
    display: grid;
    box-sizing: border-box;
    grid-template-columns: repeat(15, var(--game_grid_height));
    grid-template-rows: repeat(7, var(--game_grid_width));
    padding-left: var(--Left-Padding);
    gap: 10px; 
    justify-content: center;
  }
  

  .gridItem {
    
    grid-column: span (auto 2);
    grid-row: span (auto 2);
    min-width: var(--game_grid_width);
    min-height: var(--game_grid_height);
    /* background-color: #2f006457; */
    display: flex;
    align-items: center;
    justify-content: center;
    /* border: 1px solid #ddd; */
    box-sizing: border-box;
    border-radius: 10px;
  }

  .specialItem {
    width: auto;
    height: auto;
    aspect-ratio: 16 / 10;
    grid-column: auto / span 10;
    grid-row: auto / span 6;
    /* background-color: #e0e0e0; */
    overflow: hidden;
  }

  .Wide_Skyscraper{
    width: auto;
    height: auto;
    grid-column: 1/3;
    grid-row: 1/8;
    font-size: 24px;
    color: rgba(255, 255, 255, 0.164);
  }

  .Wide_Skyscraper2{
    width: auto;
    height: auto;
    grid-column: 14/16;
    grid-row: 1/8;
    font-size: 24px;
    color: rgba(255, 255, 255, 0.164);
    background-color: none;
  }

.Leaderboard{
    width: auto;
    height: auto;
    grid-column: 4/14;
    grid-row: 7/8;
}

.MediumBanner{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  width: auto;
  height: auto;
  grid-column: 1/4;
  grid-row: 1/8;
}

@media  (max-width: 1120px) {
  .MediumBanner{
    display: none;
  }
  .Wide_Skyscraper{
    display: none;
  }
  .Wide_Skyscraper2{
    display: none;
  }

.Leaderboard{
    display: none;
}
.gridContainer {
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: repeat(0, var(--game_grid_width));
}

}

@media  (min-width: 1120px) and (max-width: 1300px) {
  .MediumBanner{
    display: none;
  }
  .Wide_Skyscraper{
    display: none;
  }
  .Wide_Skyscraper2{
    grid-column: 11/13;
    grid-row: 1/8;
  }

.Leaderboard{
    grid-column: 1/11;
    grid-row: 7/8;
}
.gridContainer {
  grid-template-columns: repeat(12, var(--game_grid_height));
  grid-template-rows: repeat(7, var(--game_grid_width));
}

 
}


@media (min-width: 1300px)and (max-width: 1620px) {
  .MediumBanner{
    display: none;
  }
  .Wide_Skyscraper{
    display: flex;
  }
  .Wide_Skyscraper2{
    grid-column: 13/15;
    grid-row: 1/8;
  }

.Leaderboard{
    grid-column: 3/13;
    grid-row: 7/8;
}
.gridContainer {
  grid-template-columns: repeat(14, var(--game_grid_height));
  grid-template-rows: repeat(7, var(--game_grid_width));
}

 
}

@media (min-width: 1620px) {
  .MediumBanner{
    display: flex;
  }
  .Wide_Skyscraper{
    display: none;
  }
  .Wide_Skyscraper2{
    grid-column: 14/16;
    grid-row: 1/8;
  }

.Leaderboard{
    grid-column: 4/14;
    grid-row: 7/8;
}
.gridContainer {
  grid-template-columns: repeat(15, var(--game_grid_height));
  grid-template-rows: repeat(7, var(--game_grid_width));
}

 
  
}


