.container{
  margin-top: 30px;
  margin-bottom: 10px;
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: flex-start;
  justify-content: center;
  padding-left: var(--Left-Padding);
  align-content: flex-start;
}

.contant{
    border: 2px;
    padding: 20px 0px 20px 0px;
    margin-right: 10PX;
    border-radius: 10px;
    display: flex;
    background-color: #13141e;
    flex-direction: column;
    width: calc(100% - 10px);
}

.breadcrumb{
    display: flex;
    height: 10px;
    width: 100%;
    flex-direction: row;
    justify-content: flex-start;
    flex-wrap: nowrap;
    align-items: center;
    list-style-type: none;
    padding: 0px;
}
.current{
    color: white;
}
.current ::after{
    content: none !important;
}
.breadcrumb > li {
margin-left: 0px;
color: var(--white2);
font-size: 18px;
font-weight: 600;
}
.breadcrumb > li > a {
    color: var(--fontcolor3) !important;
    text-decoration: none;
    font-weight: 800;
}
.breadcrumb > li ::after{
    content: ' \00bb';
    padding: 10px;
    color: white;
}
.breadcrumb > li ::marker{
    marker: none !important;
}

/* Media Query*/
@media only screen and (min-width: 300px) {
   
    .breadcrumb {
      justify-content: center;
      }
    .breadcrumb > li {
      font-size: 12px;
      font-weight: 600;
      }
      .breadcrumb > li ::after{
        padding: 0px;
    }
  }
