.thumb_canvas_Grid{
    position: relative; /* Establishes a positioning context */
    width:100%; /* Set your desired width */
    height: 100%; /* Set your desired height */
    background-color: lightblue; /* For visibility */
   }

   .mobile_btns{
    display: flex;
    flex-direction: column;
    z-index: 3;
    position: absolute;
    height: 100%;
    width: 100%;
    justify-content: center;
    align-content: center;
    align-items: center;
  }
 

  .mobile_image{

    /* position: absolute; */
    z-index: 1;
    height: 100%;
    width: 100%;
    filter: blur(2px);
    -webkit-filter: blur(2px);
  }
  .child {
    position: absolute; /* Position children absolutely within the parent */
    width: 100%; /* Fill the width of the parent */
    height: 100%; /* Fill the height of the parent */
  }

  .child1 {
    background-color: rgba(0, 0, 0, 0.5); /* Red */
    z-index: 1; /* Lower stacking order */
  }

  .play_btn {
    width: 64px;
    height: 64px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    animation: play_btn 1s ease-in-out infinite alternate;
    -webkit-animation: play_btn 1s ease-in-out infinite alternate;
}

.play_text {
    color: var(--white);
    font-size: 30px;
    font-weight: 800;
  }
  
  @keyframes play_btn {
    0% {
      transform: scale(1);
    }
  
    50% {
      transform: scale(1.1);
    }
  
    100% {
      transform: scale(1);
    }
  }